export const CREATE_GYM_INITIAL = 'CREATE_GYM_INITIAL';
export const CREATE_GYM_REQUEST = 'CREATE_GYM_REQUEST';
export const CREATE_GYM_SUCCESS = 'CREATE_GYM_SUCCESS';
export const CREATE_GYM_FAILURE = 'CREATE_GYM_FAILURE';
export const FETCH_GYM_REQUEST = 'FETCH_GYM_REQUEST';
export const FETCH_GYM_SUCCESS = 'FETCH_GYM_SUCCESS';
export const FETCH_GYM_FAILURE = 'FETCH_GYM_FAILURE';
export const UPLOAD_GYM_IMAGES_REQUEST = 'UPLOAD_GYM_IMAGES_REQUEST';
export const UPLOAD_GYM_IMAGES_FAILURE = 'UPLOAD_GYM_IMAGES_FAILURE';
export const UPLOAD_GYM_IMAGES_SUCCESS = 'UPLOAD_GYM_IMAGES_SUCCESS';

import * as Constants from '../constants';


function createGYMInitial() {
  return {
    type: CREATE_GYM_INITIAL,
    isFetching: false,
  };
}

function requestCreateGYM(GYM) {
  return {
    type: CREATE_GYM_REQUEST,
    isFetching: true,
    GYM,
  };
}

function createGYMSuccess(GYM) {
  return {
    type: CREATE_GYM_SUCCESS,
    isFetching: false,
    GYM,
  };
}

function createGYMError(message) {
  return {
    type: CREATE_GYM_FAILURE,
    isFetching: false,
    message,
  };
}

function requestFetchGYM() {
  return {
    type: FETCH_GYM_REQUEST,
    isFetching: true,
  };
}

function fetchGYMSuccess(GYM) {
  return {
    type: FETCH_GYM_SUCCESS,
    isFetching: false,
    GYM,
  };
}

function fetchGYMError(message) {
  return {
    type: FETCH_GYM_FAILURE,
    isFetching: false,
    message,
  };
}

function requestUploadImages() {
  return {
    type: UPLOAD_GYM_IMAGES_REQUEST,
    isUploading: true,
  };
}

function uploadImagesError(message) {
  return {
    type: UPLOAD_GYM_IMAGES_FAILURE,
    isUploading: false,
    message,
  };
}

function uploadImagesSuccess(GYM) {
  return {
    type: UPLOAD_GYM_IMAGES_SUCCESS,
    isUploading: false,
    GYM,
  };
}

export function createGYM(GYM) {
  const config = {
    method: 'PUT',
    headers: {
      Accept: 'application/json',
      'Content-Type': 'application/json',
      'Authorization': localStorage.getItem('id_token'),
    },
    body: JSON.stringify(GYM),
    credentials: 'include',
  };

  return (dispatch) => {
    // We dispatch requestCreateGYM to kickoff the call to the API
    dispatch(requestCreateGYM(GYM));

    return fetch(Constants.IP + '/gym/details', config)
      .then(response =>
        response.json().then(GYM => ({ GYM, response })),
      ).then(({ GYM, response }) => {
        if (!response.ok) {
          // If there was a problem, we want to
          // dispatch the error condition
          dispatch(createGYMError(GYM.message));
          return Promise.reject(GYM);
        }
          // Dispatch the success action
        dispatch(createGYMSuccess(GYM));
        setTimeout(() => {
          dispatch(createGYMInitial());
        }, 5000);
      }).catch(err => console.log('Error: ', err));
  };
}

export function fetchGYM() {
  const config = {
    method: 'GET',
    headers: {
      Accept: 'application/json',
      // 'Content-Type': 'application/json',localStorage.setItem('id_token', user.data.token);
      // 'Authorization': 'YW1hcnZhaXNoLmlpdHJAZ21haWwuY29tOjE1MjQ1MDE0OTM0MzI=',
         'Authorization': localStorage.getItem('id_token'),
    },
    // mode: 'no-cors'
    // body: JSON.stringify({
    //   query: '{GYMs{id,title,content,updatedAt}}',
    // }),
    // credentials: 'include',
  };

  return (dispatch) => {
    dispatch(requestFetchGYM());

    return fetch(Constants.IP + '/gym/details', config)
      .then(response =>
        response.json().then(response => ({ gym: response.data, response })),
      ).then(({ gym, response }) => {
        if (!response.data) {
          // If there was a problem, we want to
          // dispatch the error condition
          dispatch(fetchGYMError(gym.message));
          return Promise.reject(gym);
        }
        // Dispatch the success action
        dispatch(fetchGYMSuccess(gym));
      }).catch(err => console.log('Error: ', err));
  };
}

export function uploadImages(images) {
  var formData = new FormData();
   // Fields in the post
   var ins = images.length;
   for (var x = 0; x < ins; x++) {
    formData.append('files', images[x]);
}

  const config = {
    method: 'POST',
    headers: {
      // Accept: 'application/json',
      // 'Content-Type': 'multipart/form-data; boundary=----WebKitFormBoundary7MA4YWxkTrZu0gW',
      'Authorization': localStorage.getItem('id_token'),
    },
    body: formData,
  };

  return (dispatch) => {
    // We dispatch requestCreateGYM to kickoff the call to the API
    dispatch(requestUploadImages(images));

    return fetch(Constants.IP + '/gym/gymPics', config)
      .then(response =>
        response.json().then(GYM => ({ GYM, response })),
      ).then(({ GYM, response }) => {
        if (!response.ok) {
          // If there was a problem, we want to
          // dispatch the error condition
          dispatch(uploadImagesError(GYM.message));
          return Promise.reject(GYM);
        }
          // Dispatch the success action
        dispatch(uploadImagesSuccess(GYM));
        setTimeout(() => {
          dispatch(createGYMInitial());
        }, 5000);
      }).catch(err => console.log('Error: ', err));
  };
}



