import {
  CREATE_GYM_INITIAL, CREATE_GYM_REQUEST, CREATE_GYM_SUCCESS, CREATE_GYM_FAILURE,
  FETCH_GYM_REQUEST, FETCH_GYM_SUCCESS, FETCH_GYM_FAILURE,
  UPLOAD_GYM_IMAGES_REQUEST,UPLOAD_GYM_IMAGES_FAILURE,UPLOAD_GYM_IMAGES_SUCCESS
} from '../actions/gym';

export default function gym(state = {
  isFetching: false,
}, action) {
  switch (action.type) {
    case CREATE_GYM_INITIAL:
      return Object.assign({}, state, {
        isFetching: false,
        message: null
      });
    case CREATE_GYM_REQUEST:
      return Object.assign({}, state, {
        isFetching: true,
      });
    case CREATE_GYM_SUCCESS:
      return Object.assign({}, state, {
        isFetching: false,
        message: 'GYM updated successfully',
        gym: action.GYM,
      });
    case CREATE_GYM_FAILURE:
      return Object.assign({}, state, {
        isFetching: false,
        message: action.message,
      });
    case FETCH_GYM_REQUEST:
      return Object.assign({}, state, {
        isFetching: true,
      });
    case FETCH_GYM_SUCCESS:
      return Object.assign({}, state, {
        isFetching: false,
        gym: action.GYM,
      });
    case FETCH_GYM_FAILURE:
      return Object.assign({}, state, {
        isFetching: false,
        message: action.message,
      });
      case UPLOAD_GYM_IMAGES_REQUEST:
      return Object.assign({}, state, {
        isUploading: true,
      });
    case UPLOAD_GYM_IMAGES_SUCCESS:
      return Object.assign({}, state, {
        isUploading: false,
        uploadMessage: action.GYM,
      });
    case UPLOAD_GYM_IMAGES_FAILURE:
      return Object.assign({}, state, {
        isUploading: false,
        uploadMessage: action.message,
      });
    default:
      return state;
  }
}
 


