import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import { ProgressBar } from 'react-bootstrap';
import { withRouter, Link } from 'react-router-dom';

import s from './Sidebar.scss';
import LinksGroup from './LinksGroup/LinksGroup';

class Sidebar extends React.Component {
  static propTypes = {
    className: PropTypes.string,
  };

  static defaultProps = {
    className: '',
  };

  render() {
    return (
  <nav className={s.root}>
    <header className={s.logo}>
      <Link to="/app">React Dashboard</Link>
    </header>

    <ul className={s.nav}>
      <LinksGroup header="Dashboard" headerLink="/app" iconName="glyphicon-tree-conifer" />
      { this.props.userType === 'ROLE_GYM' && <LinksGroup
        header="Gyms"
        headerLink="/app/posts"
        childrenLinks={[{ name: 'Show all', link: '/app/posts' }, { name: 'Gym Details', link: '/app/gym/gymDetail' }, { name: 'Create new Gym Program', link: '/app/gym/gymProgram/new' }]}
        iconName="glyphicon-list-alt"
      /> }
      <LinksGroup
        header={
          <span>
        Profile
        <sup className="text-warning fw-semi-bold">&nbsp;new</sup>
          </span>
      } headerLink="/app/profile" iconName="glyphicon-user"
      />
      <LinksGroup className="visible-xs" header="Logout" headerLink="/login" iconName="glyphicon-off" />
    </ul>

  </nav>
);
  }
  }

function mapStateToProps(state) {
  return {
    sidebarOpened: state.navigation.sidebarOpened,
    sidebarStatic: state.navigation.sidebarStatic,
    userType: state.auth.userType,
  };
}

export default withRouter(connect(mapStateToProps)(withStyles(s)(Sidebar)));



