import React from 'react';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import { Row, Col, Grid, Alert } from 'react-bootstrap';
import { connect } from 'react-redux';
import { withRouter, Redirect } from 'react-router';
import FacebookLogin from 'react-facebook-login';

import Widget from '../../components/Widget';
import Footer from '../../components/Footer';
// import s from './Login.scss'; // eslint-disable-line
import { registerUser, loginUserWithFacebook} from '../../actions/user';
import s from './Register.scss';

class Register extends React.Component {
   constructor(props){
    super(props);
    this.state={
      login: '',
      mobile: '',
      password: '',
      type: ''
    }
  }

  // static propTypes = {
  //   title: PropTypes.string.isRequired,
  // };

  changeLogin(event) {
    this.setState({ login: event.target.value });
  }

  changeMobile(event) {
    this.setState({ mobile: event.target.value });
  }

  changePassword(event) {
    this.setState({ password: event.target.value });
  }

  changeType(event) {
    this.setState({ type: event.target.value });
  }

  doRegister(e) {
    this.props
      .dispatch(registerUser({ email: this.state.login, password: this.state.password, mobile: this.state.mobile }, this.state.type));
    e.preventDefault();
  }

  responseFacebook(response) {
    if (response.status != "not_authorized" && response.status != "unknown") {
      if (response) {
        // console.log("facebook", response);
        this.props
          .dispatch(loginUserWithFacebook(response, this.state.type));
      }
    }
  }

  render() {
    var userType = this.props.userType; 

    const { from } = this.props.location.state || { from: { pathname: '/login' } };

    if (this.props.isRegistered) { // cant access login page while logged in
      return (
        <Redirect to={from}/>
      );
    }

    if (this.props.isAuthenticated && userType === 'ROLE_GYM') { // cant access login page while logged in
     const { from } = { from: { pathname: '/app/gym' } };
      return (
        <Redirect to={ from }/>
      );
    } 

    if (this.props.isAuthenticated && userType === 'ROLE_CUSTOMER') { // cant access login page while logged in
     const { from } = { from: { pathname: '/app/customer' } }; 
      return (
        <Redirect to={ from }/>
      );
    }

    return (
       <div className={s.root}>
        <Grid>
          <Row>
            <Col xs={10} xsOffset={1} sm={6} smOffset={3} lg={4} lgOffset={4}>
              <p className="text-center">React Dashboard</p>
              <Widget className={s.widget}>
                <h4 className="mt-0">Sign Up</h4>
                <form className="mt" onSubmit={this.doRegister.bind(this)}>
                  {
                    this.props.errorMessage && (
                      <Alert className="alert-sm" bsStyle="danger">
                        {this.props.errorMessage}
                      </Alert>
                    )
                  }
                  <div className="form-group">
                    <input className="form-control no-border" value={this.state.login} onChange={this.changeLogin.bind(this)} type="text" required name="username" placeholder="Email" />
                  </div>
                  <div className="form-group">
                    <input className="form-control no-border" value={this.state.mobile} onChange={this.changeMobile.bind(this)} type="text" required name="mobile" placeholder="Mobile No" />
                  </div>
                  <div className="form-group">
                    <input className="form-control no-border" value={this.state.password} onChange={this.changePassword.bind(this)} type="password" required name="password" placeholder="Password" />
                  </div>
                  <div className="form-group">
                    <input type="radio" name="type" value="gym" onChange={this.changeType.bind(this)} /> Gym 
                    <input type="radio" name="type" value="customer" onChange={this.changeType.bind(this)} /> Customer
                  </div>  
                  <div className="clearfix">
                    <div className="btn-toolbar pull-right">
                      <button type="submit" className="btn btn-success btn-sm">{this.props.isFetching ? 'Loading...' : 'Register'}</button>
                    </div>
                  </div>
                </form>
                <br></br>
                 <FacebookLogin
                         appId="345501322630290"
                         autoLoad={false}
                         icon="fa-facebook"
                         fields="name,email,picture"
                         scope="public_profile,user_friends"
                         textButton="Or Sign up with Facebook"
                         disableMobileRedirect={true}
                         callback={this.responseFacebook.bind(this)}
                         size={"small"}
                                         >
                      </FacebookLogin>
                <div className="clearfix">       
                   <a className="mt-sm pull-right fs-sm" href="/login">Already have a account Sign in?</a>
                </div>
              </Widget>
            </Col>
          </Row>
        </Grid>
        <Footer className="text-center" />
      </div>
    );
  }
}

function mapStateToProps(state) {
  return {
    isFetching: state.auth.isFetching,
    isRegistered: state.auth.isRegistered,
    isAuthenticated: state.auth.isAuthenticated,
    userType: state.auth.userType,
    errorMessage: state.auth.errorMessage
  };
}


export default withRouter(connect(mapStateToProps)(withStyles(s)(Register)));



